@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');

* {
    font-family: "Roboto Condensed", sans-serif;    
    margin: 0;
    padding: 0;    
    box-sizing: border-box;
    color: #fff;
}

.container{
    width: 1300px;
    padding: 0 10px;
    margin: 0 auto;
}

ul,li,dl {    
    list-style-type: none;
    margin: 0;    
    padding: 0;
}
body{
    background: #1C242D;
}
h1,h2,
h3,h4,
h5,h6 {
    margin: 0;}
a {
    text-decoration: none;    
    color: inherit;
    display: inline-block;    
    &:hover {
        text-decoration: none;       
         color: inherit;
    }}

p{
    margin: 0;   
     padding: 0;
}

input::-webkit-outer-spin-button,input::-webkit-inner-spin-button {
    -webkit-appearance: none;    
    margin: 0; 
}

.app {    width: 1100px;
    margin: 0 auto;   
    padding: 50px 0 50px 0;
    position: relative;    
    
    .bg-decoration {
        position: absolute;        
        right: -174px;
        bottom: -70px;        
        z-index: -1;
}}


main {
    margin-top: 50px;    
    position: relative;
}
